const inputButton = document.getElementById("input-button")
const inputBox = document.getElementById("input-box")
inputButton.addEventListener("click", function() {
    console.log(inputBox.value)
    try {
        const ast = parse(inputBox.value)
        console.log(JSON.stringify(ast, null, 2))
        // traverse bottom-up   
        let inqueue = []
        inqueue.push(ast) // add node to end
        let outqueue = []
        while (inqueue.length > 0) {
            let node = inqueue.shift() // remove node from beginning
            let usedVariables = {}

            if (node["mathjs"] === "OperatorNode") {
                if (!validOperations.includes(node["op"])) {
                    throw `operator ${node["op"]} isn't recognized`
                }
                outqueue.unshift(node) // add node to beginning

                for (child in node["args"]) {
                    inqueue.push(child)
                }
            } else {
                if (!Number.isInteger(node["name"])) {
                    throw `symbol ${node["name"]} isn't recognized`
                }
                usedVariables[Math.abs(parseInt(node["name"]))] = 0 
            }
        }

        // SAT = ""
        // while (outqueue.length > 0) {
        //     let node = outqueue.shift()


        // }

    } catch (err) {
        inputButton.innerText = err.message
        setTimeout(()=>{
            inputButton.innerText = "calculate"
        }, 2000)
    }  
})

const data = {
  name: "A",
  children: [
    { name: "B", children: [{ name: "C" }, { name: "D" }] },
    { name: "E", children: [{ name: "F" }] },
  ],
};

const { node:node1, g:g1 } = chart(data);
const { node:node2, g:g2 } = chart(data);

document.getElementById('dpll-chart').append(node1);
document.getElementById('cdcl-chart').append(node2);

const zoom1 = d3.zoom()
    .scaleExtent([1, 8])
    .on("zoom", (event) => {
        g1.attr("transform", event.transform)
    });

const zoom2 = d3.zoom()
    .scaleExtent([1, 8])
    .on("zoom", (event) => {
        g2.attr("transform", event.transform)
    });

d3.select(node1).call(zoom1);
d3.select(node2).call(zoom2);